<template>
  <div class="corporate_structure_node">

    <div class="current_node_container">

      <!-- button to expand or contract the node -->
      <span
        class="expand_button mdi mdi-chevron-right"
        v-bind:class="expand_button_class"
        v-on:click="toggle_node()"/>


      <span
        class="node_name_container"
        v-on:click="$emit('nodeSelected',nodeData._fields[0].identity.low)">
        {{nodeData._fields[0].properties.original_name}}
      </span>

    </div>

    <!-- The children nodes -->
    <div v-if="open" class="children_container">

      <CorporateStructureNode
        v-for="child in children"
        v-bind:key="child._fields[0].identity.low"
        v-bind:personalInformation="personalInformation"
        v-on:nodeSelected="$emit('nodeSelected',$event)"
        v-bind:nodeData="child"/>

    </div>

   </div>
</template>

<script>
import CorporateStructureNode from './CorporateStructureNode.vue'


export default {
  name: 'CorporateStructureNode',
  components: {
    CorporateStructureNode,
  },
  props: {
    nodeData: Object,
    personalInformation: Object,
  },
  data(){
    return{
      open: false,
      children: []
    }
  },
  mounted(){

    // Auto opening of user's units
    if(this.personalInformation){
      // Get the label of the node
      let node_label = this.nodeData._fields[0].labels[0]

      // Find the node of the user info that has the same label
      let field_key = this.personalInformation._fieldLookup[node_label.toLowerCase()]
      let matching_user_unit = this.personalInformation._fields[field_key]

      // Check if the user unit matches the node
      if(matching_user_unit.identity.low === this.nodeData._fields[0].identity.low){
        this.open_node();
      }
    }

  },
  methods: {
    open_node(){
      this.open = true;

      this.axios.post('http://authentication.mike.jtekt.maximemoreillon.com/get_units_directly_belonging_to_node', {
        node_id: this.nodeData._fields[0].identity.low,
      })
      .then(response => this.children = response.data)
      .catch(error => console.log(error));
    },
    close_node(){
      this.open = false;
    },
    toggle_node(){
      if(this.open) this.close_node();
      else this.open_node();
    }
  },
  computed: {
    expand_button_class(){
      if(this.open) return "chevron_open"
      else return "chevron_closed"
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


.current_node_container {
  display: flex;
  align-items: center;
  margin: 1px;
}

.current_node_container > * {

  cursor: pointer;
}


.expand_button{
  /* center button in div */

  margin-right: 10px; /* space between button and name */

  width: 18px;
  height: 18px;

  font-size: 140%;

  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;


  transition: transform 0.25s;
}

.chevron_open{
  transform: rotate(90deg);
}

.node_name_container {
  flex-grow: 1;
  transition: color 0.25s;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.node_name_container:hover{
  color: #c00000;
}

.children_container{

  /* content of this is a CorporateStructureNode */

  /* compound spacing for border indicating ident */
  padding-left: 9px; /* indent for children nodes */
  margin-left: 9px; /* indent for children nodes */
  border-left: 1px solid #dddddd;
}





</style>
