<template>
  <div class="employee_picker">

    <!-- nested divs for headers -->
    <div class="employee_picker_column">

      <!-- header of the group picking side-->
      <div class="">
        <span class="mdi mdi-account-group"/>
      </div>

      <div class="">
        <CorporateStructureNode
          v-for="division in divisions"
          v-bind:nodeData="division"
          v-bind:key="division._fields[0].identity.low"
          v-bind:personalInformation="personal_information"
          v-on:nodeSelected="get_employees_belonging_to_node($event)"/>
      </div>
    </div>

    <div class="employee_picker_column">
      <!-- header of the employee picking side-->
      <div class="">
        <span class="mdi mdi-account"/>
      </div>

      <div class="">
        <table class="employee_table" v-if="employees.length > 0">
          <tr
            v-for="employee in employees"
            v-bind:key="employee._fields[0].identity.low"
            v-on:click="$emit('employeeSelected',employee)">

            <td class="name_cell">{{employee._fields[0].properties.name_kanji}}</td>
            <td class="employee_number_cell">{{employee._fields[0].properties.employee_number}}</td>
            <td>
              <span
                class="mdi mdi-account-search"
                v-on:click.stop="show_profile_of(employee._fields[0].properties.employee_number)"/>
              </td>

          </tr>
        </table>
        <table v-else>
          <tr>
            <td class="">No data</td>
          </tr>
        </table>
      </div>

    </div>

  </div>
</template>

<script>

import CorporateStructureNode from './CorporateStructureNode.vue'


export default {
  name: 'EmployeePicker',
  components: {
    CorporateStructureNode
  },
  data(){
    return {
      divisions: [],
      employees: [],
      personal_information: null,
    }
  },
  mounted(){
    this.axios.post('http://authentication.mike.jtekt.maximemoreillon.com/personal_information_v2', {})
    .then(response => {
      this.personal_information = response.data[0];

      this.axios.post('http://authentication.mike.jtekt.maximemoreillon.com/get_all_divisions', {})
      .then(response => this.divisions = response.data)
      .catch(error => console.log(error));

    })
    .catch(error => console.log(error));


  },
  methods: {
    // SORTING COULD BE DONE IN NEO4J
    get_employees_belonging_to_node(node_id){
      this.axios.post('http://authentication.mike.jtekt.maximemoreillon.com/get_employees_belonging_to_node', {node_id: node_id})
      .then(response => this.employees = response.data.sort((a, b) => { return a._fields[0].properties.employee_number - b._fields[0].properties.employee_number;}))
      .catch(error => console.log(error));
    },

    show_profile_of(employee_number){
      window.open("http://employees.mike.jtekt.maximemoreillon.com/?employee_number=" + String(employee_number), '_blank');
    },
  },
  computed: {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.employee_picker{

  /* side by side layout with wrap if necessary*/
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.employee_picker > .employee_picker_column {

  margin: 15px;

  /* horizontal growth */
  flex-grow: 1;
  flex-basis: 0;
  flex-shrink: 0;

  min-width: 300px;

  border: 1px solid #dddddd;
}



.employee_picker > .employee_picker_column > div {
  padding: 10px;

  /* Defining height is valid here, the containers will fit */
  /* especially good for dealing with overflows */
  max-height: 300px;
  overflow-y: auto;
}

/* Section title */
.employee_picker > .employee_picker_column > div:first-child {
  border-bottom: 1px solid #dddddd;
}

.employee_table{
  border-collapse: collapse;
  width: 100%;
  text-align: center;
  table-layout: fixed;
}

.employee_table tr {
  cursor: pointer;
  transition: background-color 0.25s;
}

.employee_table tr:not(:last-child){
  border-bottom: 1px solid #dddddd;
}

.employee_table tr:hover {
  background-color: #eeeeee;
}

.employee_table td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

}

.name_cell {
  width: 60%
}
</style>
